.App {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    /* text-align: center; */
    background-color: #444;
    direction: rtl;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    // background-color: rgba(0, 0, 0, 0.6);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

button {
    border: none;
    border-width: 0;
    background-color: transparent;
    color: #ddd;
    pointer-events: all;
    text-align: center;
    &:hover {
        color: white;
        background: #666;
    }

    * {
        pointer-events: none;
    }
}

.buttonsBar {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    align-items: stretch;
    padding: 5px 4px;
    flex-grow: 1;
    min-height: 40px;
    button {
        margin: 0 1px;
        padding: 0;
        .ModalContent .Title & {
            margin: 0 1px;
        }

        flex-grow: 1;
        font-size: 15px;
        background: #444;
        &:hover {
            color: white;
            background: #666;
        }
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

hr {
    border-style: solid;
    border-width: 1px 0;
    border-color: #222 #888 #888 #222;
}

.iBlock {
    display: inline-block;
    margin: 0 5px;
}
